body{
  width: 100%;
}

.table{
  width: 400px !important;
}
.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

hr{
  border-top: 1px solid white;
}

.btn-with-margins{
  margin: 10px;
}

.admin-user{
  margin-left: 20px;
}

.App-admin {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 20px;
  color: white;
}

.form-width{
  min-width: 600px;
}

.message-font{
  font-size: 10px;
}

.margin-top{
  margin-top: 20px;
}

.text-center{
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

